
.project-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  color: #ffffff;
  font-size: 15px;
  margin: 10px;
  width: 100%;
  }
  
.project-info {
  display: flex;
  flex-direction: column;
  margin-top: 160px;
  margin-left: 40px;
  font-family: 'Bungee Inline', cursive;
  }

.features-heading {
 font-size: 15px;
 letter-spacing: 1px;
}

.project-description {
  font-size: 30px;
  font-weight: 900;
  margin-top: -10px;
  font-family: 'Bungee Hairline', cursive;
}

.project-description-byline {
  font-size: 20px;
  font-weight: 900;
  margin-top: -25px;
  margin-bottom: 34px;
  font-family: 'Bungee Hairline', cursive;
}

.project-video {
  display: flex;
  flex-direction: column;
  width: 900px;
  object-fit: cover;
  margin: 80px;
}

.external-links {
 display: flex;
 flex-direction: row;
 outline: none;
 justify-content: space-evenly;
 text-decoration: none;
}

ul {
display: flex;
flex-direction: column;
font-family: 'Bungee Hairline', cursive;
font-size: 15px;
  }

li {
  padding-top: 10px;
}

a {
outline: none;
text-decoration: none;
color: rgb(255,162,190);
}
